/**
 * WordPress dependencies
 */
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { isUrlFragmentInvalid } from '@nab/utils';

export function getLocalUrlError( url = '', homeUrl?: string ): string | false {
	if ( ! homeUrl ) {
		return false;
	} //end if

	if ( 0 !== url.indexOf( homeUrl ) ) {
		return sprintf(
			/* translators: a URL */
			_x(
				'URL doesn’t start with your WordPress’ home URL (i.e. “%s”)',
				'text',
				'nelio-ab-testing'
			),
			homeUrl
		);
	} //end if

	if ( isUrlFragmentInvalid( url.replace( homeUrl, '' ) ) ) {
		return _x(
			'Please type in a valid URL to track',
			'user',
			'nelio-ab-testing'
		);
	} //end if

	return false;
} //end getLocalUrlError()
